"use client";

import { useState } from "react";
import { useComposeStore } from "@/lib/store/composeStore";
import { createClient } from "@/lib/supabase/client";
import { validateTextContent, validateSongUrl } from "@/lib/validation";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

export function StepReview({ onSuccess }: { onSuccess: () => void }) {
  const draft = useComposeStore((s) => ({
    deliverMode: s.deliverMode,
    deliverAt: s.deliverAt,
    birthdayDay: s.birthdayDay,
    birthdayMonth: s.birthdayMonth,
    timezone: s.timezone,
    messageType: s.messageType,
    textContent: s.textContent,
    selectedPromptIds: s.selectedPromptIds,
    guidedMode: s.guidedMode,
    songTitle: s.songTitle,
    songArtist: s.songArtist,
    songUrl: s.songUrl,
    mediaBlob: s.mediaBlob,
    editMessageId: s.editMessageId,
  }));
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const supabase = createClient();

  const deliverAt = draft.deliverMode === "birthday"
    ? (draft.birthdayDay && draft.birthdayMonth
        ? `Your birthday (${draft.birthdayMonth}/${draft.birthdayDay})`
        : "Birthday (set month/day)")
    : draft.deliverAt
      ? new Date(draft.deliverAt).toLocaleString()
      : "—";

  const handleConfirm = async () => {
    setLoading(true);
    setError(null);
    const textErr = draft.messageType === "text" ? validateTextContent(draft.textContent) : null;
    const songErr = validateSongUrl(draft.songUrl);
    if (textErr || songErr) {
      setError(textErr ?? songErr ?? null);
      setLoading(false);
      return;
    }
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        setError("Not signed in");
        setLoading(false);
        return;
      }
      let deliverAtDate: string;
      if (draft.deliverMode === "birthday" && draft.birthdayDay != null && draft.birthdayMonth != null) {
        const now = new Date();
        let y = now.getFullYear();
        let d = new Date(y, draft.birthdayMonth - 1, draft.birthdayDay);
        if (d <= now) d = new Date(y + 1, draft.birthdayMonth - 1, draft.birthdayDay);
        deliverAtDate = d.toISOString();
      } else if (draft.deliverAt) {
        deliverAtDate = new Date(draft.deliverAt).toISOString();
      } else {
        setError("Set delivery date");
        setLoading(false);
        return;
      }
      const prompts = (draft.selectedPromptIds ?? []).map((id) => ({ id, text: "" }));
      const payload = {
        type: draft.messageType ?? "text",
        text_content: draft.messageType === "text" ? (draft.textContent ?? null) : null,
        deliver_at: deliverAtDate,
        deliver_mode: draft.deliverMode ?? "date",
        birthday_day: draft.birthdayDay ?? null,
        birthday_month: draft.birthdayMonth ?? null,
        timezone: draft.timezone ?? null,
        guided_mode: draft.guidedMode ?? true,
        selected_prompts: prompts,
        song_title: draft.songTitle ?? null,
        song_artist: draft.songArtist ?? null,
        song_url: draft.songUrl ?? null,
      };
      let messageId: string;
      if (draft.editMessageId) {
        const { error: updateErr } = await supabase
          .from("messages")
          .update(payload)
          .eq("id", draft.editMessageId)
          .eq("user_id", user.id);
        if (updateErr) {
          setError(updateErr.message);
          setLoading(false);
          return;
        }
        messageId = draft.editMessageId;
        if (draft.mediaBlob) {
          const ext = "webm";
          const path = `${user.id}/${messageId}/media.${ext}`;
          const { error: uploadErr } = await supabase.storage
            .from("capsule-media")
            .upload(path, draft.mediaBlob, { upsert: true });
          if (!uploadErr) {
            await supabase.from("messages").update({ media_path: path }).eq("id", messageId);
          }
        }
      } else {
        const { data: msg, error: insertErr } = await supabase
          .from("messages")
          .insert({
            user_id: user.id,
            ...payload,
            media_path: null,
            status: "scheduled",
          })
          .select("id")
          .single();
        if (insertErr) {
          setError(insertErr.message);
          setLoading(false);
          return;
        }
        messageId = msg!.id;
        if (draft.mediaBlob) {
          const ext = "webm";
          const path = `${user.id}/${messageId}/media.${ext}`;
          const { error: uploadErr } = await supabase.storage
            .from("capsule-media")
            .upload(path, draft.mediaBlob, { upsert: true });
          if (uploadErr) {
            setError(uploadErr.message);
            setLoading(false);
            return;
          }
          await supabase.from("messages").update({ media_path: path }).eq("id", messageId);
        }
      }
      useComposeStore.getState().reset();
      onSuccess();
    } catch (e) {
      setError(e instanceof Error ? e.message : "Something went wrong");
    }
    setLoading(false);
  };

  return (
    <div className="space-y-6">
      <Card className="bg-muted/50">
        <CardContent className="p-4 space-y-2 text-sm">
          <p><strong>Delivery:</strong> {deliverAt}</p>
          <p><strong>Type:</strong> {draft.messageType ?? "—"}</p>
          <p><strong>Guided prompts:</strong> {(draft.selectedPromptIds ?? []).length}</p>
          <p><strong>Song link:</strong> {draft.songUrl ? "Yes" : "No"}</p>
        </CardContent>
      </Card>
      {error && <p className="text-sm text-red-600">{error}</p>}
      <Button onClick={handleConfirm} disabled={loading}>
        {loading ? "Saving…" : "Confirm & save"}
      </Button>
    </div>
  );
}
